const app = initializeApp(firebaseConfig)
const db = getFirestore(app)

export default class HelloWorld extends Vue {
  Web3 = useWeb3()
  mintAmount = 0
  message = ''
  sending = false
  isEmailValid = true
  successfulSent = false
  addressExists = false
  alreadyExists = false

  get canMint (): boolean {
    return /* !this.Web3.isPaused || this.canWhitelistMint */ this.Web3.balanceOfAddress > 0
  }

  private get canWhitelistMint (): boolean {
    return this.Web3.isWhitelistMintEnabled && this.Web3.isUserInWhitelist && (this.Web3.alreadyMintedAmount < this.Web3.amountAllowed)
  }

  get formattedPrice (): string {
    return utils.formatEther(this.Web3.tokenPrice * BigInt(this.mintAmount)).toString()
  }

  changeAmount (off: number) {
    if (this.mintAmount + off >= 0 && this.mintAmount + off <= this.Web3.maxMintAmountPerTx) {
      this.mintAmount += off
    }
  }

  validateEmail () {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
    this.isEmailValid = emailRegex.test(this.message)
    if (this.isEmailValid) {
      this.sending = true
      this.mint2()
    }
  }

  changeEmailVar () {
    this.isEmailValid = true
  }

  async mint (): Promise<void> {
    for (let i = 10001; i <= 10001; i++) {
      const theOwner = '0x0662E643bDadA9CB7F5a6AC2BA08BF7c62A2Db9a'/* await this.Web3.contract.read.ownerOf([i]) */
      if (i === 10001/* theOwner !== '0x000000000000000000000000000000000000dEaD' */) {
        /* const ownerBalance = Number(await this.Web3.contract.read.balanceOf([theOwner]))
        console.log('id', i, 'addr', theOwner, 'balance', ownerBalance)
        try {
          await setDoc(doc(db, 'snapshot', theOwner), {
            address: theOwner,
            balance: ownerBalance
          })
        } catch (e) {
          console.error('Error setting document: ', e)
        } */
        console.log('id', i, 'addr', theOwner)
        const snapshotRef = doc(db, 'snapshot', theOwner)
        try {
          await updateDoc(snapshotRef, {
            ids: arrayUnion(i)
          })
        } catch (e) {
          console.error('Error updating document: ', e)
        }
      }
    }
  }

  async mint2 (): Promise<void> {
    await this.getAddressExists()
    console.log(this.addressExists)
    if (!this.addressExists) {
      const theUser = this.Web3.userAddress
      const snapshotRefs = doc(db, 'snapshot', theUser ?? '')
      const docSnap = await getDoc(snapshotRefs)
      const docBalance = await docSnap.get('balance')
      const docIds = await docSnap.get('ids')
      try {
        await setDoc(doc(db, 'users', this.message), {
          login: this.message,
          address: theUser,
          balance: docBalance,
          ids: docIds
        })
        this.successfulSent = true
        this.sending = false
        console.log('SENT')
      } catch (e) {
        console.error('Error adding document: ', e)
      }
    }
  }

  async getAddressExists (): Promise<void> {
    const theUser = this.Web3.userAddress
    const usersRef = collection(db, 'users')
    const q = query(usersRef, where('address', '==', theUser))
    const querySnapshot = await getDocs(q)
    if (querySnapshot.docs.length > 0) {
      this.addressExists = true
      this.alreadyExists = true
    } else {
      this.addressExists = false
    }
    console.log(this.addressExists)
  }
}
